import React, { Component } from 'react'
import { Upload, Icon, Modal } from 'antd'
import { uploadPaper, deletePaper, downloadZip } from '/api/global'
import { dealBlob, isImage } from '/api/tools'

class PaperWall extends Component {

  async componentWillReceiveProps (nextprops) {
    if (nextprops.config != this.props.config && nextprops.config.visible) {
      this.setState({fileList: this.props.fileList})
    }
  }

  state = {
    previewVisible: false,
    previewURL: '',
    isImage: true,
    fileName: '',
    fileList: []
  };

  handleCancel = () => this.setState({ previewVisible: false })

  handlePreview = (file) => {
    console.log(file)
    let suffix = file.name.split('.')[1]
    this.setState({
      fileName: file.name,
      previewURL: file.url || file.thumbUrl,
      isImage: isImage(suffix),
      previewVisible: true,
    });

  }

  handleRemove = async (file) => {
    let result = true
    await deletePaper({uid: file.uid}).then(res => {
      if(res.code != 200){
        message.error('图片删除失败')
        result = false
      }
    })
    return result
  }

  handleChange = ({ file, fileList }) => { 
    if(file.status == 'done'){
      //file.uid = file.response.data
      fileList.forEach(o => {
        if (o.uid == file.uid) {
          o.uid = file.response.data
        }
      })
    }
    this.setState({ fileList })
  }

  handleOk = _ => {
    downloadZip({type: this.props.type, dir: this.props.thumb}).then(blob => {
      dealBlob(blob, "影像文件.zip")
    })
  }

  render() {
    const uploadButton = (
      <div>
        <Icon type="plus" />
        <div className="ant-upload-text">文件上传</div>
      </div>
    );
    return (
      <Modal title='文件管理'
    onOk={this.handleOk}
    visible={this.props.config.visible}
    confirmLoading={this.state.loading}
    onCancel={this.props.onCancel}
    okText="下载全部"
    width={610}
    style={{top: 50, marginBottom: 100}}
    cancelText="取消">
      <div className="clearfix">
        <Upload
          action={`${uploadPaper}`}
          listType="picture-card"
          data={{id: this.props.id,type: this.props.type,dirpath: this.props.path,thumb: this.props.thumb}}
          multiple={true}
          fileList={this.state.fileList}
          onPreview={this.handlePreview}
          onChange={this.handleChange}
          onRemove={this.handleRemove}
          showUploadList={{showPreviewIcon:true, showRemoveIcon:true }}
        >
          {uploadButton}
        </Upload>
        <Modal visible={this.state.previewVisible} footer={null} 
        onCancel={this.handleCancel}
        width={560}>
        { this.state.isImage ?
          <img alt={this.state.fileName} style={{ width: '100%' }} src={this.state.previewURL} />
          : <embed src={this.state.previewURL} style={{ width: '100%', minHeight: '600px' }} />
        }
        </Modal>
      </div>
      </Modal>
    );
  }
}

export default PaperWall


